import { Component, OnInit } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { ModaltipsPage } from 'src/app/modals/modaltips/modaltips.page';
import { TipscmanhaPage } from 'src/app/modals/tipscmanha/tipscmanha.page';
import { TipslmanhaPage } from 'src/app/modals/tipslmanha/tipslmanha.page';
import { TipsalmocoPage } from 'src/app/modals/tipsalmoco/tipsalmoco.page';
import { TipslanchePage } from 'src/app/modals/tipslanche/tipslanche.page';
import { TipsjantaPage } from 'src/app/modals/tipsjanta/tipsjanta.page';
import { TipsceiaPage } from 'src/app/modals/tipsceia/tipsceia.page';

@Component({
  selector: 'app-daily-meal',
  templateUrl: './daily-meal.page.html',
  styleUrls: ['./daily-meal.page.scss'],
})
export class DailyMealPage implements OnInit {

  constructor(private modalCtrl: ModalController) { }

  async ngOnInit() {
    const modal = await this.modalCtrl.create({
      component: ModaltipsPage,
      cssClass:'tips_modal'
    });
    modal.present();
  }


  async dicaCManha() {
    const modal = await this.modalCtrl.create({
      component: TipscmanhaPage,
      cssClass:'cmanha_modal'
    });
    modal.present();
  }

  async dicaLManha() {
    const modal = await this.modalCtrl.create({
      component: TipslmanhaPage,
      cssClass:'lmanha_modal'
    });
    modal.present();
  }

  async dicaAlmoco() {
    const modal = await this.modalCtrl.create({
      component: TipsalmocoPage,
      cssClass:'almoco_modal'
    });
    modal.present();
  }

  async dicaLanche() {
    const modal = await this.modalCtrl.create({
      component: TipslanchePage,
      cssClass:'lanche_modal'
    });
    modal.present();
  }

  async dicaJanta() {
    const modal = await this.modalCtrl.create({
      component: TipsjantaPage,
      cssClass:'janta_modal'
    });
    modal.present();
  }

  async dicaCeia() {
    const modal = await this.modalCtrl.create({
      component: TipsceiaPage,
      cssClass:'ceia_modal'
    });
    modal.present();
  }

}
